import React, { useState, useEffect } from "react";

import axios from "axios";

export default function Profile() {
  const [data, setData] = useState({});
  let temp = null;

  const token = localStorage.getItem("token");
  if (!token) {
    window.location.href = "/login";
  }

  console.log(token);

  axios
    .get(`https://devgroceryapi.spericorn.com/api/user`, {
      headers: { Authorization: `Bearer ${token}` },
    })
    .then((res) => {
      if (res.status === 200) {
        console.log(res);

        temp = { ...temp, Userid: res.data.data.userData.id };

        temp = { ...temp, Firstname: res.data.data.userData.firstName };

        temp = { ...temp, Lastname: res.data.data.userData.lastName };

        temp = { ...temp, username: res.data.data.userData.username };

        temp = { ...temp, email: res.data.data.userData.email };

        temp = { ...temp, Phone: res.data.data.userData.phone };

        setData(temp);
      } else console.log("errrrrrrrrrr", res);
    });

  const logoutaction = () => {
    localStorage.removeItem("token");
    window.location.href = "/login";
  };

  return (
    <div className="profile">
      <h1>Welcome {data.username}</h1>

      <p>Userid: {data.Userid}</p>

      <p>Firstname: {data.Firstname}</p>

      <p>Lastname: {data.Lastname}</p>

      <p>username: {data.username}</p>

      <p>Email: {data.email}</p>

      <p>Phone : {data.Phone}</p>

      <button onClick={logoutaction}>Logout</button>
    </div>
  );
}
