import React from "react";
import Header from "./Header";
import Footer from "./Footer";

import Fansignup from "../components/auth/Fansignup";
import Talentsignup from "../components/auth/Talentsignup";

import {
  Tabs,
  Tab,
  Modal,
  Row,
  Button,
  Col,
  Form,
  Card,
  Container,
} from "react-bootstrap";

export default function Homepage() {
  return (
    <div style={{ height: "100%", display: "flex", flexDirection: "column" }}>
      <Header />
      <div
        className="row d-flex justify-content-center bg-dark"
        style={{ minHeight: "80vh" }}
      >
        <div className="col-md-6 bg-light">
          <Container
            style={{ display: "flex", flexDirection: "column" }}
            className=" mx-auto"
          >
            <Row className="row d-flex justify-content-center mx-auto">
              <Col>
                <Tabs defaultActiveKey="Home" id="controlled-tab-example">
                  <Tab eventKey="Home" title="FAN SIGNUP">
                    <Fansignup />
                  </Tab>
                  <Tab eventKey="profile" title="TALENT SIGNUP">
                    <Talentsignup />
                  </Tab>
                </Tabs>
              </Col>
            </Row>
          </Container>
        </div>
      </div>
      <Footer />
    </div>
  );
}
