import React from "react";
import logo from "./logo.png";
import Axios from "axios";
import { MDBCol, MDBIcon } from "mdbreact";
import { Accordion, Card, Button } from "react-bootstrap";

export default function Header() {
  return (
    <div className="row bg-dark text-white" style={{ Height: "10vh" }}>
      <div className="col-md-12">
        <div className="row">
          <div className="col-md-9">
            <img src={logo} style={{ width: "264px", marginLeft: "10px" }} />
          </div>
          <div className="col-md-2">Sign Up</div>
          <div className="col-md-1">Login</div>
        </div>
      </div>
      <div className="col-md-12">
        A marketplace for conversations,mentorships and perfoemances.
      </div>
      <div className="col-md-12 ">
        <div className="row ">
          <div className="col-md-4"> Search New Talent</div>
          <div className="col-md-4">
            <div className="input-group rounded">
              <input
                type="text"
                className="form-control border-secondary rounded-pill pr-5"
                placeholder="Search this blog"
              />
              {/* <div className="input-group-append">
                <button className="btn btn-secondary" type="button">
                  <i className="fas fa-search"></i>
                </button>
              </div> */}
            </div>
          </div>
          <div className="col-md-4"> </div>
        </div>
      </div>
    </div>
  );
}
