import React, { useState, useEffect } from "react";
import "./styles.css";
import axios from "axios";

const Talentsignup = () => {
  const [data, setData] = useState({});
  let temp = null;
  const [errors, setErrors] = useState({});

  function checkPassword(str) {
    var re = /^(?=.*[!@#$%^&*]).{8,}$/;
    return re.test(str);
  }

  console.log(checkPassword(data.password));

  const registerapires = () => {
    console.log("hellooo");
    if (checkPassword(data.password) === false) {
      console.log(errors);

      console.log("if enterered");
      temp = {
        ...errors,
        passworderr:
          "Password, must have at least 8 characters and a special symbol a Number and 1Caps ",
      };
    }

    axios
      .post(`https://admin.fanconvo.com/api/v3/sign-up/fan`, {
        ...data,
        timezone: "America/New_York",
        captcha: true,
        phone: 9495399999,
      })
      .then((res) => {
        console.log(data);
        // window.location.href = "/login";
        temp = {
          ...errors,
          apiresmessage: res.data.message,
        };
        console.log(res);
      })
      .catch((error) => console.log(error));

    //   setErrors();

    console.log(temp);

    setErrors(temp);
  };

  return (
    <div style={{ height: "100%", display: "flex", flexDirection: "column" }}>
      <h1>Create Your Talent Account</h1>
      <form
        style={{ display: "flex", flexDirection: "column" }}
        className=" mx-auto"
      >
        <div>
          <input
            type="text"
            value={data.first_name}
            onChange={(e) => {
              setData({ ...data, first_name: e.target.value });
            }}
            placeholder="first_name"
            required
          />
        </div>
        <div>
          <input
            type="text"
            value={data.last_name}
            onChange={(e) => {
              setData({ ...data, last_name: e.target.value });
            }}
            placeholder="last_name"
            required
          />
        </div>
        <div>
          <input
            type="text"
            value={data.username}
            onChange={(e) => {
              setData({ ...data, username: e.target.value });
            }}
            placeholder="username"
            required
          />
        </div>
        <div>
          <input
            type="text"
            value={data.email}
            onChange={(e) => {
              setData({ ...data, email: e.target.value });
            }}
            placeholder="enter email"
            required
          />
        </div>
        <div>
          <input
            type="password"
            value={data.password}
            onChange={(e) => {
              setData({ ...data, password: e.target.value });
            }}
            placeholder="enter password"
            required
          />
        </div>
        {/* <div>
          <input
            style={{ display: "none" }}
            type="number"
            value={data.timezone}
            onChange={(e) => {
              setData({ ...data, timezone: "America/New_York" });
            }}
            placeholder="Choose timezone"
          />
        </div>
setData({ ...data, timezone: "America/New_York", captcha: true });
 setData({ ...data, captcha: true });
        <div>
          <input
            style={{ display: "none" }}
            type="password"
            value={data.captcha}
            onChange={(e) => {
              setData({ ...data, captcha: true });
            }}
            placeholder="captcha"
          />
        </div> */}

        <button
          onClick={(e) => {
            e.preventDefault();
            registerapires();
          }}
        >
          SIGN UP
        </button>
      </form>
      <p>{errors?.apiresmessage}</p>
      <p>{errors?.passworderr}</p>
    </div>
  );
};

export default Talentsignup;
